.charge-wrap{
    width:100%;
    height:100%;
    margin:0 auto;
}
.charge-header{
    width:100%;
    height:50px;
    padding:0 15px;
    background: #2b5295;
}
.charge-header h1{
    float: left;
    width:100px;
}
.charge-header h1 img{
    width:auto;
    height:50px;
    display: block;
}
.charge-header>ul{
    float: left;
    margin:0 90px;
    height: 100%;
}
.charge-header>ul li{
    float: left;
    height:50px;
    width:100px;
}
.charge-header>ul li a{
    display: block;
    height:50px;
    width:100px;
    text-align: center;
    line-height: 50px;
    color:#fff;
    font-size: 15px;
}
/* .charge-header>ul li:hover{
    background: #1e3d73;
} */
.charge-header ul li a.active{
    background: #1e3d73;
}
.charge-header .user{
    float: left;
    line-height: 50px;
    color:#fff;
}
.charge-header .info{
    float:right;
    line-height: 50px;
    color:#fff;
}
.charge-header .info .num{
    position: relative;
    top:-15px;
    /* width:15px;
    height:15px; */
}
.charge-header .info .ant-scroll-number{
    /* width:100%;
    height:100%; */
}